      </el-col>
      <el-col :span="12">
        <el-form-item prop="project_code">
          <el-input v-model="ruleForm.project_code" :disabled="true">
            <template slot="prepend">案件CD</template>
          </el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-form-item prop="estimate_code">
          <el-input v-model="ruleForm.estimate_code" :disabled="true">
            <template slot="prepend">見積CD</template>
          </el-input>
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item prop="order_code">
