            <template slot="prepend">標題の件につき</template>
          </el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-form-item prop="customer_name">
          <el-input v-model="ruleForm.customer_name">
            <template slot="prepend">得意先名</template>
          </el-input>
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item prop="customer_address">
          <el-input v-model="ruleForm.customer_address">
            <template slot="prepend">得意先住所</template>
          </el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-form-item prop="work">
          <div>作業内容</div>
          <el-input v-model="ruleForm.work" type="textarea" autosize>
          </el-input>
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item prop="personnel1">
          <div>主任担当者</div>
          <el-input v-model="ruleForm.personnel1" type="textarea" :rows="2">
          </el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-form-item prop="work_time">
          <el-input v-model="ruleForm.work_time">
            <template slot="prepend">作業期間/納期</template>
          </el-input>
        </el-form-item>
      </el-col>
      <!-- <el-col :span="24">
        <el-form-item prop="personnel2">
          <el-input v-model="ruleForm.personnel2">
            <template slot="prepend">主任担当者(乙)</template>
          </el-input>
        </el-form-item>
      </el-col> -->
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-form-item prop="deliverables">
          <el-input v-model="ruleForm.deliverables">
            <template slot="prepend">納入物</template>
          </el-input>
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item prop="commission">
          <el-input v-model="ruleForm.commission">
            <template slot="prepend">委託料</template>
          </el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-form-item prop="deliverable_space">
          <el-input v-model="ruleForm.deliverable_space">
            <template slot="prepend">納入場所</template>
          </el-input>
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item prop="payment_date">
          <el-input v-model="ruleForm.payment_date">
            <template slot="prepend">支払期日</template>
