      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="12">
        <el-form-item prop="other">
          <el-input v-model="ruleForm.other">
            <template slot="prepend">その他</template>
          </el-input>
        </el-form-item>
      </el-col>
      <el-col :span="12">
        <el-form-item prop="acceptance_conditions">
          <el-input v-model="ruleForm.acceptance_conditions">
            <template slot="prepend">検収条件</template>
          </el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="24">
        <el-form-item label="" prop="note">
          <div>※(注)</div>
          <el-input v-model="ruleForm.note" type="textarea" :rows="2">
          </el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="24">
        <el-form-item>
          <el-button-group>
            <el-button
              type="warning"
              @click="cancelForm()"
              icon="el-icon-document-delete"
              plain
              >閉じる</el-button
            >
            <el-button
              type="primary"
              @click="submitForm('ruleForm')"
              icon="el-icon-document-checked"
              plain
              >登録・更新</el-button
            >
          </el-button-group>
        </el-form-item>
      </el-col>
